import { KeyOutlined } from "@ant-design/icons";
import { Modal, Button, Divider, Typography } from "antd";
import { useMutation } from "@apollo/react-hooks";
import { CREATE_BOOKING } from "../../../../lib/graphql/mutations";
import { CreateBooking as CreateBookingData, CreateBookingVariables } from "../../../../lib/graphql/mutations/CreateBooking/__generated__/CreateBooking";
import { displayErrorMessage, displaySuccessNotification } from "../../../../lib/utils";
import moment, { Moment } from 'moment';
import { CardElement, injectStripe, ReactStripeElements } from 'react-stripe-elements'
import { formatListingPrice } from '../../../../lib/utils';

interface Props {
  price: number;
  checkInDate: Moment;
  checkOutDate: Moment;
  modalVisible: boolean;
  setModalVisible: (modaleVisible: boolean) => void;
  id: string;
  clearBookingData: () => void
  handleListingRefetch: () => Promise<void>
}

const { Paragraph, Text, Title } = Typography;

export const ListingCreateBookingModal = ({
  modalVisible,
  setModalVisible,
  checkInDate,
  checkOutDate,
  price,
  id,
  clearBookingData,
  handleListingRefetch,
  stripe
}: Props & ReactStripeElements.InjectedStripeProps) => {
  const [createBooking, { loading }] = useMutation<
    CreateBookingData,
    CreateBookingVariables
    >(CREATE_BOOKING, {
      onCompleted: () => {
        clearBookingData();
        displaySuccessNotification(
          "You've succesfully booked the listing!",
          "Booking history can always be found in your User page."
        );
        handleListingRefetch();
      },
      onError: () => {
        displayErrorMessage("Sorry! We weren't able to succesfully book the listing. Please try again later.")
      }
  });

  const daysBooked = checkOutDate.diff(checkInDate, "days") + 1;
  const listingPrice = price * daysBooked;

  const handleCreateBooking = async () => {
    if (!stripe) {
      return displayErrorMessage("Sorry! We weren't able to connet with Stripe.");
    }

    const { token: stripeToken, error } = await stripe.createToken();
    if (stripeToken) {
      createBooking({
        variables: {
          input: {
            id,
            source: stripeToken.id,
            checkIn: moment(checkInDate).format("YYYY-MM-DD"),
            checkOut: moment(checkOutDate).format("YYYY-MM-DD"),
          }
        }
      })
    } else {
      displayErrorMessage(
        error && error.message
          ? error.message :
          "Sorry! We weren't able to book the listing. Please try again later."
      )
    }
  }

  return (
    <Modal
      visible={modalVisible}
      centered
      footer={null}
      onCancel={() => setModalVisible(false)}
    >
      <div className="listing-booking-modal">
        <div className="listing-booking-modal__intro">
          <Title className="listing-booking-modal__intro-title">
            <KeyOutlined />
          </Title>
          <Title level={3} className="listing-booking-modal__intro-title">
            Book your trip
          </Title>
          <Paragraph>
            Enter your payment informatiON to book the listing from the dates between{" "}
            <Text strong mark>{moment(checkInDate).format("MMMM Do YYYY")}</Text> and{" "}
            <Text strong mark>{moment(checkOutDate).format("MMMM Do YYYY")}</Text>
            , inclusive.
          </Paragraph>
        </div>

        <Divider />

        <div className="listing-booking-modal__charge-summary">
          <Paragraph>
            {formatListingPrice(price, false)} * {daysBooked} days ={" "}
            <Text strong>{formatListingPrice(listingPrice, false)}</Text>
          </Paragraph>
          <Paragraph className="listing-booking-modal__charge-summary-total">
            Total = <Text mark>{formatListingPrice(listingPrice, false)}</Text>
          </Paragraph>
        </div>

        <Divider />

        <div className="listing-booking-modal__stripe-card-section">
          <CardElement
            className="listing-booking-modal__stripe-card"
            hidePostalCode
          />
          <Button
            size="large"
            type="primary"
            className="listing-booking-modal__cta"
            onClick={handleCreateBooking}
            loading={loading}
          >
            Book
          </Button>
        </div>
      </div>
    </Modal>
  );
}

export const WrappedListingCreateBookingModal = injectStripe(ListingCreateBookingModal);