import { Button, Menu, Avatar } from "antd";
import { Link } from "react-router-dom";
import { useMutation } from "@apollo/react-hooks";
import { Viewer } from "../../../../lib/types";
import { HomeOutlined, UserOutlined, LogoutOutlined } from '@ant-design/icons';
import { LogOut as LogOutData } from "../../../../lib/graphql/mutations/LogOut/__generated__/LogOut";
import { LOG_OUT } from "../../../../lib/graphql/mutations";
import { displaySuccessNotification, displayErrorMessage } from "../../../../lib/utils";

const { Item, SubMenu } = Menu;
interface Props {
  viewer: Viewer;
  setViewer: (viewer: Viewer) => void;
}

export const MenuItems = ({ viewer, setViewer }: Props) => {
  const [logOut] = useMutation<LogOutData>(LOG_OUT, {
    onCompleted: (data) => {
      if (data && data.logOut) {
        setViewer(data.logOut);
        sessionStorage.removeItem("token");
        displaySuccessNotification("You've successfully logged out!");
      }
    },
    onError: () => {
      displayErrorMessage("Sorry! We weren't able to log you out. Please try again later!");
    }
  });

  const handleLogOut = () => {
    logOut();
  };
  
  const subMenuLogin = viewer.id ? (
    <SubMenu title={<Avatar src={viewer.avatar}/>} key="/logged-in" >
      <Item key="/user">
        <Link to={`/user/${viewer.id}`} >
          <UserOutlined style={{marginRight: '8px'}}/>
          Profile
        </Link>
      </Item>
      <Item key="/logout">
        <div onClick={handleLogOut}>
          <LogoutOutlined style={{marginRight: '8px'}}/>
          Logout
        </div>
      </Item>
    </SubMenu>
  ) : (
    <Item key="/login">
      <Link to="/login">
        <Button type="primary" >Sign In</Button>
      </Link>
    </Item>
  );

  return (
    <Menu disabledOverflow={true} mode="horizontal" selectable={false} className="menu" >
      <Item key="/host">
        <Link to="/host">
          <HomeOutlined style={{marginRight: '8px'}}/>
          Host
        </Link>
      </Item>
      {subMenuLogin}
    </Menu>
  );
}